.navbar-light .navbar-nav .nav-link {
  color: $text-color-dark;
  display: flex;
  align-items: center;
}

.navigation {
  transition: 0.3s ease;
}

.navbar {
  transition: 0.3s ease;
  &-collapse {
    justify-content: end;
    margin-right: 120px;
    @include desktop {
      padding-bottom: 20px;
    }
  }
}

.nav-bg {
  background-color: $white;
  box-shadow: 0px 10px 20px 0px rgba(0, 141, 236, 0.1);

  .navbar {
    padding: 10px 0;
    // padding: 42px 0 10px;
  }
}

.nav-item {
  padding: 20px 10px;
  @include desktop {
    padding: 0;
  }
}

// ========== 移动端适配 ===========
@include desktop {
  .navbar-collapse {
    position: fixed;
    top: 56px; // header高度
    left: 0;
    right: 0;
    bottom: 0;
    background: $white;
    z-index: 1050;
    width: 100vw;
    height: calc(100vh - 56px);
    overflow-y: auto;
    box-shadow: 0 4px 24px rgba(0, 0, 0, 0.08);
    padding: 24px 0 0 0;
    margin-right: 0;
    text-align: center;
    transition: transform 0.3s;
  }
  .navbar-collapse:not(.show) {
    display: none !important;
  }
  .navbar-nav {
    flex-direction: column;
    width: 100%;
    align-items: center;
    padding-left: 24px;
    padding-right: 24px;
  }
  .nav-item {
    width: 100%;
    padding: 0;
    margin-bottom: 8px;
  }
  .nav-link {
    width: 100%;
    font-size: 16px;
    padding: 12px 0;
    border-bottom: 1px solid #f0f0f0;
    text-align: center;
    justify-content: center;
  }
  .dropdown-menu {
    position: static !important;
    float: none;
    background: #f9f9f9;
    box-shadow: none;
    width: 100%;
    margin: 0;
    padding: 8px 0 8px 8px;
    border-radius: 0;
    display: none;
    max-width: 100vw;
    overflow-x: auto;
    font-size: 15px;
    word-break: break-all;
    white-space: normal;
  }
  .dropdown-title {
    font-size: 13px;
    font-weight: 600;
    color: #888;
    margin-bottom: 4px;
    padding-left: 4px;
    padding-top: 2px;
    text-align: center;
  }
  .dropdown-columns {
    display: flex;
    flex-direction: column;
    gap: 2px;
  }
  .dropdown-item {
    font-size: 14px;
    padding: 8px 0 8px 8px;
    line-height: 1.2;
    max-width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    border-radius: 4px;
    transition: background 0.2s;
    text-align: center;
  }
  .dropdown-item:hover,
  .dropdown-item:active {
    background: #ececec;
  }
  .nav-item.show > .dropdown-menu {
    display: block;
  }
  .dropdown-toggle::after {
    float: right;
  }
  .navbar-toggler {
    position: absolute;
    right: 16px;
    top: 12px;
    z-index: 1100;
    border: none;
    outline: none;
  }
}

