/* 1.1 typography */
@import url("https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700");

body {
  line-height: 31px;
  font-weight: 300;
  -webkit-font-smoothing: antialiased;
  font-size: 17px;
  
  @include mobile {
    font-size: 14px;
    line-height: 24px;
  }
  
  @include tablet {
    font-size: 15px;
    line-height: 26px;
  }
}

p,
.paragraph {
  font-weight: 300;
  color: $text-color;
  font-size: 17px;
  line-height: 31px;
  
  @include mobile {
    font-size: 14px;
    line-height: 24px;
  }
  
  @include tablet {
    font-size: 15px;
    line-height: 26px;
  }
}

h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  color: $text-color-dark;
  font-weight: 600;
  line-height: 1.5;
}

h1,
.h1 {
  font-size: 56px;
  
  @include mobile {
    font-size: 32px;
  }
  
  @include tablet {
    font-size: 42px;
  }
}

h2,
.h2 {
  font-size: 43px;
  
  @include mobile {
    font-size: 28px;
  }
  
  @include tablet {
    font-size: 34px;
  }
}

h3,
.h3 {
  font-size: 37px;
  
  @include mobile {
    font-size: 24px;
  }
  
  @include tablet {
    font-size: 30px;
  }
}

h4,
.h4 {
  font-size: 22px;
  
  @include mobile {
    font-size: 18px;
  }
  
  @include tablet {
    font-size: 20px;
  }
}

h5,
.h5 {
  font-size: 16px;
  
  @include mobile {
    font-size: 14px;
  }
}

h6,
.h6 {
  font-size: 13px;
  
  @include mobile {
    font-size: 12px;
  }
}
